<template>
  <Layout activeMenu="appMgr">
    <Table :columns="columns1" :data= "data1"></Table>
  </Layout>
</template>

<script>

  import $ from 'jquery';



  export default {
    data () {
      return {
        columns1: [
          {
            title: 'id',
            key: 'id'
          },
          {
            title: '应用名称',
            key: 'name'
          },
          {
            title: '操作',
            key: 'action',
            width: 150,
            align: 'center',
            render: function (h, params) {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: function () {
                      alert(params.index);
                    }
                  }
                }, '删除')
              ])
            }
          }

        ],
        data1: [
          {
            id: 1,
            name: 'test'
          },
          {
            id: 2,
            name: 'test2'
          }
        ]
      }
    }
  }
</script>

<style>
</style>



